/*------------------------------------------------------------------
[5@mixin  Typography]
*/

/* Standard elements
--------------------------------------------------
*/

@import url("https://fonts.googleapis.com/css?family=Montserrat:300,400,500,600");
@import url("https://keplerapis.com/fonts/css?family=Inter+UI:300,400,500,600");

html {
    font-size: 100%;
    -ms-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%;
}
body {
    color: $color-contrast-high;
    font-family: $base-font-family;
    font-size: 14px;
    font-weight: normal;
    letter-spacing: 0.01em;
    -webkit-font-smoothing: antialiased;
    -webkit-text-size-adjust: 100%;
    -ms-text-size-adjust: 100%;
    -webkit-font-feature-settings: "kern" 1;
    -moz-font-feature-settings: "kern" 1;
}
/* Headings
------------------------------------
*/
h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 10px 0;
    font-family: $base-font-family;
    -webkit-font-smoothing: antialiased;
    -webkit-text-size-adjust: 100%;
    -ms-text-size-adjust: 100%;
    font-weight: 500;
    color: inherit;
}
h1 {
    font-size: 44px;
    line-height: 50px;
    letter-spacing: -0.0141279em;
}
h2 {
    font-size: 30px;
    line-height: 38px;
    letter-spacing: -0.021em;
}
h3 {
    font-size: 27px;
    line-height: 35px;
    letter-spacing: -0.0114923em;
}
h4 {
    font-size: 22px;
    line-height: 32px;
    letter-spacing: -0.00865734em;
}
h5 {
    font-size: 18px;
    line-height: 26px;
    letter-spacing: -0.00630069em;
}
h3 small,
h4 small,
h5 small {
    font-weight: 300;
}
h1.block,
h2.block,
h3.block,
h4.block,
h5.block,
h6.block {
    padding-bottom: 10px;
}
/* Lins and Others
------------------------------------
*/
a {
    text-shadow: none !important;
    color: $color-primary-dark;
    transition: color 0.1s linear 0s, background-color 0.1s linear 0s,
    opacity 0.2s linear 0s !important;
    font-weight: 500;
}
a:focus,
a:hover,
a:active {
    color: $color-primary;
}
a,
a:focus,
a:hover,
a:active {
    outline: 0 !important;
    text-decoration: none;
}
a[ng-click] {
    cursor: pointer;
}
br {
    line-height: normal;
    clear: both;
}
p {
    display: block;
    color: inherit;
    font-size: 14px;
    font-weight: normal;
    letter-spacing: 0.00177646em;
    line-height: 21px;
    margin: 0px 0px 10px 0px;
    font-style: normal;
    white-space: normal;
}
small,
.small {
    line-height: 18px;
    font-size: 85.714%;
}
label {
    &.inline {
        display: inline-block;
        position: relative;
        top: 0px;
        font-size: 13px;
    }
}
ul,
ol {
    margin-bottom: 10px;
    & > li {
        @include padding-left(3px);
        line-height: 24px;
    }
    &.lg-icon {
        & > li {
            font-size: 21px;
            & span {
                font-size: 14px;
            }
        }
    }
    &.no-style {
        list-style: none;
        @include padding-left(5px);
    }
}

address {
    margin-bottom: 0px;
    a {
        color: $color-contrast-high;
    }
}
blockquote {
    padding: 4px 0 0 18px;
    @include border-left( 0 );
    &:before {
        content: "\e95d";
        font-family: "pages-icon";
        font-size: 20px;
        @include margin-right(6px);
        @include float(left);
        position: relative;
        top: -12px;
    }
    p {
        font-size: 16px;
        margin-bottom: 4px;
    }
    small {
        line-height: 29px;
        color: $color-contrast-high;
        &:before {
            content: "";
            @include margin-right( 6px );
        }
    }
    &.pull-right {
        @include border-right( 0 );
        &:before {
            float: right;
            font-family: "pages-icon";
            content: "\e95d";
            @include margin-left( 6px );
            @include margin-right( 0 );
        }
        small {
            @include padding-right( 30px );
            &:after {
                content: "";
            }
        }
    }
}
hr {
    border-color: $color-contrast-low;
    &.double {
        border-width: 2px;
    }
    &.dotted {
        border-style: dotted none none;
    }
}

.ff-sup {
    font-feature-settings: "kern" 1, "sups" 1;
    -webkit-font-feature-settings: "kern" 1, "sups" 1;
    -ms-font-feature-settings: "kern" 1, "sups" 1;
    -moz-font-feature-settings: "kern" 1, "sups" 1;
}

.ff-sub {
    font-feature-settings: "kern" 1, "subs" 1;
    -webkit-font-feature-settings: "kern" 1, "subs" 1;
    -ms-font-feature-settings: "kern" 1, "subs" 1;
    -moz-font-feature-settings: "kern" 1, "subs" 1;
}

/* Standard HTML Typography tags
------------------------------------
*/

code {
    color: $color-contrast-medium;
    background-color: $color-contrast-lower-a;
    font-size: 97%;
    position: relative;
    line-height: inherit;
    border-radius: 3px;
    padding: 5px 7px;
    margin: 0;
    &:hover {
        color: $color-complete;
    }
    &.code-sm{
        padding: 3px 6px;
    }
}

figcaption {
    font-size: 13px;
    margin-top: 10px;
    font-weight: 400;
    display: block;
    letter-spacing: 0.008em;
    text-align: center;
    color: $color-contrast-medium;
    line-height: 1.46;
}

em {
    font-style: italic !important;
    font-family: inherit;
    font-weight: inherit;
}

ins {
    font-family: $base-font-family;
    border-bottom: 1px solid #d0d0d0;
    text-decoration: none;
    color: $color-contrast-high;
    font-weight: normal;
    font-size: 94%;
}

cite {
    font-family: $base-font-family;
    font-weight: 300;
    color: $color-contrast-medium;
    text-decoration: none;
    font-style: normal;
    hanging-punctuation: first;
}

sup {
    top: -0.28em;
    font-size: 70%;
}

sub {
    bottom: 0.03em;
}

var {
    font-family: $base-font-family;
    font-feature-settings: "calt" 1, "tnum" 1, "frac" 1, "case" 1, "ss01" 1,
    "cv11" 1;
}

abbr {
    text-decoration: none;
    letter-spacing: 0.01em;
    background-color: transparent;
    border-bottom: 2px solid #ffba5a;
    transition-property: color, background, border;
    transition-duration: 0.15s;
    transition-timing-function: linear;
    color: $color-contrast-high;
    border-bottom-color: #ffba5a;
    -webkit-text-stroke: 0.18px #ffba5a;
}

q {
    font-family: $base-font-family;
    font-weight: 500;
    font-size: 20px;
    hanging-punctuation: first;

    &:before {
        content: "\201C";
        color: rgba(0, 0, 0, 0.44);
        font-size: 35px;
    }

    &:after {
        content: "\201D";
        font-size: 35px;
        color: rgba(0, 0, 0, 0.44);
    }
}
audio {
    margin-top: 42px;
}
hr {
    clear: both;
    margin-bottom: 42px;
    margin-top: 42px;
    border: 0;
    border-bottom: 1px solid rgba(0, 0, 0, 0.13);
}
address {
    font-family: $base-font-family;
    font-style: normal;
    margin: 0 0 1.75em;
    font-size: 14px;
    line-height: 24px;
    margin-top: 24px;
}
abbr {
    text-decoration: none;
    letter-spacing: 0.01em;
    background-color: transparent;
    border-bottom: 2px solid #ffba5a;
    transition-property: color, background, border;
    transition-duration: 0.15s;
    transition-timing-function: linear;
    text-decoration: none;
    color: $color-contrast-high;
    border-bottom-color: #ffba5a;
    -webkit-text-stroke: 0.18px #ffba5a;
}

acronym {
    text-decoration: none;
    text-transform: uppercase;
    letter-spacing: 0.06em;
    background-color: transparent;
    border-bottom: 2px solid #ffba5a;
    transition-property: color, background, border;
    transition-duration: 0.15s;
    transition-timing-function: linear;
    text-decoration: none;
    color: $color-contrast-high;
    border-bottom-color: #ffba5a;
    -webkit-text-stroke: 0.2px #ffba5a;
}

pre {
    tab-size: 4;
    font-size: 85.714%;
    overflow-x: auto;
    font-family: monospace, monospace;
    line-height: 1.7;
    counter-reset: line;
    background-color: $color-contrast-lower;
    color: $color-contrast-high;
    margin: 0px;
    counter-reset: line;
    padding: 17px 20px;
    border-radius: 3px;
    margin-top: 20px;
    margin-bottom: 20px;
}

dt {
    font-weight: 700;
    margin-bottom: 5px;
}

dd {
    margin: 0 0 1.75em;
}

kbd {
    font-size: 85.714%;
    font-family: $base-font-family;
    position: relative;
    line-height: 34px;
    top: -3px;
    letter-spacing: 0.01em;
    padding: 5px 7px;
    margin: 0;
    color: rgba(0, 0, 0, 0.53);
    background-color: #fff;
    border-radius: 3px;
    box-shadow: 0px 2px 0px 1px #c7c7c7, 0px 1px 0px 1px rgba(0, 0, 0, 0.15),
    0px 0px 0px 1px #ececec;
}

/* Types
------------------------------------
*/

.overline {
    text-transform: uppercase;
    display: inline-block;
    letter-spacing: 0.06em;
    font-size: 11px;
}

/* Font Sizes
------------------------------------
*/

.small-text {
    font-size: 12px !important;
    letter-spacing: 0.00849077em;
    line-height: 18px;
    a {
        text-decoration: underline;
    }
}
.normal-text {
    font-size: 13px !important;
}
.large-text {
    font-size: 15px !important;
}

/* Font Weights
------------------------------------
 */

.normal {
    font-weight: normal !important;
}
.semi-bold {
    font-weight: 500 !important;
}
.bold {
    font-weight: 600 !important;
}
.light {
    font-weight: 300 !important;
}

/* Misc
------------------------------------
*/

.logo {
    margin: 18px 14px;
}
.all-caps {
    text-transform: uppercase;
    letter-spacing: 0.07em !important;
}
.text-uppercase {
    text-transform: uppercase !important;
    letter-spacing: 0.07em !important;
}
.muted {
    color: $color-contrast-medium;
}
.hint-text {
    opacity: 0.76 !important;
}
.no-decoration {
    text-decoration: none !important;
}

.text-ellipsis {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

/* Monochrome Colors
------------------------------------
 */
.bg-contrast-higher {
    background-color: $color-contrast-higher !important;
