/* Gradients
------------------------------------
*/
.gradient-grey {
    background: -webkit-linear-gradient(
            top,
            rgba(0, 0, 0, 0) 0%,
            rgba(0, 0, 0, 0.8) 75%
    );
    background: linear-gradient(
            to bottom,
            rgba(0, 0, 0, 0) 0%,
            rgba(0, 0, 0, 0.8) 75%
    );
}
.gradient-black {
    background: -webkit-linear-gradient(
            top,
            rgba(0, 0, 0, 0) 0%,
            rgba(0, 0, 0, 0.8) 75%
    );
    background: linear-gradient(
            to bottom,
            rgba(0, 0, 0, 0) 0%,
            rgba(0, 0, 0, 0.8) 75%
    );
}

/* Other Colors
------------------------------------
*/
.bg-black {
    background-color: $color-black !important;
    color: text-contrast($color-black);
}
.bg-white {
    background-color: $color-contrast-lowest !important;
    color: $color-contrast-high;
}
.bg-transparent {
    background-color: transparent !important;
}

/* Text Colors */
.link {
    opacity: 0.7;
    &:hover {
        opacity: 1;
    }
}

.text-dark {
    color: $color-contrast-higher !important;
}
.text-color {
    color: $color-contrast-high !important;
}
.text-primary {
    color: fix-contrast($color-primary) !important;
}
.text-complete {
    color: fix-contrast($color-complete) !important;
}
.text-success {
    color: fix-contrast($color-success) !important;
}
.text-info {
    color: fix-contrast($color-info) !important;
}
.text-warning {
    color: fix-contrast($color-warning) !important;
}
.text-danger {
    color: fix-contrast($color-danger) !important;
}
.text-black {
    color: $color-black !important;
}
.text-white {
    color: $color-white !important;
}

.text-orange {
    color: $color-base-orange !important;
}

.text-blue {
    color: $color-base-blue !important;
}

.positive-light {
    color: $emerald;
}

.positive {
    color: $darkEmerald;
}

.negative {
    color: $darkRed;
}

/* Text Aligngments
------------------------------------
*/

.text-right {
    @include text-align(right !important);
}
.text-left {
    @include text-align(left !important);
}
.text-center {
    text-align: center !important;
}

/* Labels
------------------------------------
*/

.label {
    padding: 3px 5px;
    font-size: 11px;
    line-height: 1;
    text-shadow: none;
    background-color: $color-contrast-low;
    font-weight: 600;
    color: $color-contrast-high;
    border-radius: 3px;
}
.label-success {
    background-color: $color-success;
    color: $color-success-text;
}
.label-warning {
    background-color: $color-warning;
    color: text-contrast($color-warning);
}
.label-important,
.label-danger {
    background-color: $color-danger;
    color: text-contrast($color-danger);
}
.label-info {
    background-color: $color-complete;
    color: text-contrast($color-complete);
}
.label-inverse {
    background-color: $color-complete-dark;
    color: text-contrast($color-complete-dark);
}
.label-white {
    background-color: #fff;
    color: $color-contrast-high;
}

/* Font Sizes
------------------------------------
tracking =a+be(c  z)

a, b and c are constants
a = -0.0223
b = 0.185
c = -0.1745
z = font size

*/
.fs-10 {
    font-size: 10px !important;
    letter-spacing: 0.0180093em;
    line-height: 15px;
}
.fs-11 {
    font-size: 10.5px !important;
    letter-spacing: 0.0128359em;
    line-height: 16px;
}
.fs-12 {
    font-size: 12px !important;
    letter-spacing: 0.00849077em;
    line-height: 18px;
}
.fs-13 {
    font-size: 13px !important;
    letter-spacing: 0.00484144em;
    line-height: 19px;
}
.fs-14 {
    font-size: 14px !important;
    letter-spacing: 0.00177646em;
    line-height: 22px;
}
.fs-15 {
    font-size: 15px !important;
    letter-spacing: -0.000797757em;
    line-height: 23px;
}
.fs-16 {
    font-size: 16px !important;
    letter-spacing: -0.00295978em;
    line-height: 24px;
}

/* Line-heights
------------------------------------
*/
.lh-normal {
    line-height: normal;
}
.lh-10 {
    line-height: 10px;
}
.lh-11 {
    line-height: 11px;
}
.lh-12 {
    line-height: 12px;
}
.lh-13 {
    line-height: 13px;
}
.lh-14 {
    line-height: 14px;
}
.lh-15 {
    line-height: 15px;
}
.lh-16 {
    line-height: 16px;
}

/* Font Faces
------------------------------------
*/

.font-arial {
    font-family: Arial, sans-serif !important;
}
.font-montserrat {
    font-family: $secondary-font-family !important;
}
.font-georgia {
    font-family: Georgia !important;
}
.font-heading {
    font-family: $secondary-font-family;
}
.font-secondary {
    font-family: $secondary-font-family;
}

/* Page title
------------------------------------
*/
.page-title {
    h3 {
        margin-bottom: 1em;
    }

    i {
        color: $color-menu-light;
        display: inline-block;
        @include float(left);
        font-size: 30px;
        margin-right: 12px;
        position: relative;
        top: 3px;
    }
}

/* Wells
------------------------------------
*/
.well {
    background-color: $color-contrast-low;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    border-radius: 3px;
    -webkit-box-shadow: none !important;
    -moz-box-shadow: none !important;
    box-shadow: none !important;
    border: none;
    background-image: none;
    &.well-large {
        padding: 24px;
        width: auto;
    }
    &.well-small {
        padding: 13px;
        width: auto;
    }
    &.green {
        background-color: $color-complete;
        color: $color-contrast-lowest;
        border: none;
    }
}
.overflow-ellipsis {
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
}

.jumbotron {
    @include border-radius(0);
    p {
        font-size: 14px;
        font-weight: normal;
        margin-bottom: inherit;
        &.small {
            font-size: 85.714%;
        }
    }
}

/* Responsive Handlers : Typo
------------------------------------
*/

@media (max-width: 1400px) {
    body,
    p {
        font-size: 13px;
        line-height: 20px;
    }

    h1 {
        font-size: 33px;
        line-height: 44px;
        letter-spacing: -0.08px;
    }
    h2 {
        font-size: 28px;
        line-height: 40px;
    }
