/* Widths */

$mq-xs: min-width 0px;
$mq-sm: min-width 576px;
$mq-md: min-width 768px;
$mq-lg: min-width 992px;
$mq-xl: min-width 1200px;
$mq-xlg: min-width 1824px;

$mq-not-xs: max-width 319px;
$mq-not-sm: max-width 575px;
$mq-not-md: max-width 767px;
$mq-not-lg: max-width 991px;
$mq-not-xl: max-width 1119px;
$mq-not-xlg: max-width 1823px;

/* Heights */

$mq-x-short: min-height 400px;
$mq-short: min-height 500px;
$mq-tall: min-height 600px;
$mq-x-tall: min-height 700px;

$mq-not-x-short: max-height 399px;

/* Orientations */
$mq-portrait: orientation portrait;
$mq-landscape: orientation landscape;

/* Capabilities */
$mq-hover-yes: "hover: hover";
$mq-hover-no: hover none;
